import React, { Component } from "react";
import { useTranslation } from "react-i18next";

const imgMyimageexample = require("./img/cover.jpg");
const divStyle = {
  margin: 0,
  padding: 0,
  backgroundImage: `url(${imgMyimageexample})`,

  backgroundRepeat: "no-repeat",
  backgroundSize: "cover",
  backgroundAttachment: "fixed",
};

function ComingSoon() {
  const [t, i18n] = useTranslation();
  return (
    <section className="landing">
      <div className="landing-inner">
        <img src="" />
        <h2 className="mt-5">Your Medical Portal is</h2>
        <h1 className="mt-5">Coming Soon</h1>
        <h2 className="my-5">Click below to fill out our Treatment Request Form!</h2>
        <div className="justify-content-center banner-header text-center mt-5">
          <a
            href="https://forms.zohopublic.com/Medical-Portal/form/ServiceOrderForm/formperma/p2evCkxsmL2PtNfhs2EwXqAyhQhmW9IEeV3SaScjGFo"
            className="btn btn-primary mt-5"
          >
            {t("Home.FillForm")}
          </a>
        </div>
        <div className="countdown"></div>
      </div>
    </section>
  );
}

export default ComingSoon;
